import { ROOT_PATH } from '../constants'

export const GAUGE_INITIALIZE = "GAUGE_INITIALIZE"
export const GAUGE_INITIALIZE_SUCCESS = "GAUGE_INITIALIZE_SUCCESS"
export const GAUGE_INITIALIZE_FAILURE = "GAUGE_INITIALIZE_FAILURE"

export const gaugeInitialize = () => {
    return async(dispatch, getState) => {
        dispatch({type: GAUGE_INITIALIZE})

        try {
            const types = []
            await detectUsedTypes(ROOT_PATH, types)
            return dispatch({type: GAUGE_INITIALIZE_SUCCESS, types})
        } catch (err) {
            return dispatch({type: GAUGE_INITIALIZE_FAILURE, err})
        }
    }
}

const detectUsedTypes = async(path, types) => {
    const folder = await cozy.client.files.statByPath(path)
    const content = folder.relations('contents')

    for(let child of content) {
        if(child.attributes && child.attributes.type && child.attributes.type === 'directory') {
            await detectUsedTypes(child.attributes.path, types)
        } else {
            if(child.attributes.tags && child.attributes.tags.length > 0) {
                const type = child.attributes.tags[0]
                let find = false
                for(let t of types) {
                    if(t.type === type) {
                        t.count = t.count + 1 || 1
                        find = true
                        break
                    }
                }
                if(!find) {
                    types.push({type, count:1})
                }
            }
        }
    }
}


