import { connect } from 'react-redux'
import { documentsInitialize, gaugeInitialize } from '../actions'
import App from '../App'

const mapDispatchToProps = dispatch => {
    return {
        initilizeGauge: () => {
            dispatch(gaugeInitialize())
        },
        initilizeDocs: () => {
            dispatch(documentsInitialize())
        }
    }
}

const ConnectedApp = connect(
    null,
    mapDispatchToProps
)(App)

export default ConnectedApp


