import { connect } from 'react-redux'
import { importerClick } from '../actions'
import Gauge from '../components/elements/Gauge'

import { getFileTypes } from '../scripts/fileTypes'

const getList = ({ usedTypes }) => {
    const types = getFileTypes()
    const notUse = types.filter(f => {
        let find = false
        for(let t of usedTypes) {
            if(t.type === f.key) find = true
        }
        return !find
    })
    return notUse.slice(0, 3)
}

const mapStateToProps = state => {
    return {
        list: getList(state.gauge),
        usedType: state.gauge.usedTypes,
        status: state.gauge.status
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onFileClick: (path, type) => {
            dispatch(importerClick(path, type))
        }
    }
}

const ConnectedGauge = connect(
    mapStateToProps,
    mapDispatchToProps
)(Gauge)

export default ConnectedGauge


