import React, {Component} from 'react'
import style from '../../styles/Gauge.sass'
import Portal from 'react-portal'

import Modal from '../modals/Modal'
import Importer from '../../containers/Importer'

import { getFileTypes } from '../../scripts/fileTypes'
import { STATUS_DONE, STATUS_ERROR, STATUS_LOAD } from '../../constants'

export default class Gauge extends Component {

    constructor(props, context) {
        super(props, context)

        this.fileTypes = getFileTypes()

        this.state = {
            open: false
        }
    }

    componentWillMount () {
    }

    click(key, location) {
        this.props.onFileClick(location, key)
        this.setState({open: true}) 
    }

    render() {
        let width = 0
        if(this.props.usedType) {
            const used = this.props.usedType.map(f => f.type)
            width = (used.length / this.fileTypes.length) * 100
        }

        return (
            <div className={style.main}>
                <div className={style.title}>
                    <span>Remplissez votre HOMEBOOK :</span>
                    {this.props.status === STATUS_LOAD && (<span className={style.loader}><img src="/images/progress.gif" alt="loader"/></span>)}
                </div>
                <div className={style.gauge} title={`Jauge : ${width.toFixed(1)}%`}>
                    <div className={style.bar} style={{width: width+'%'}}></div>
                </div>
                <ul className={style.list}>
                    {this.props.list.map((l, key) => (
                        <li key={key} onClick={(e) => this.click(l.key, l.location)}>
                            <i className="ion-ios-plus-empty"></i>{l.name}
                        </li>
                    ))}
                </ul>

                <Portal isOpened={this.state.open} closeOnEsc onClose={() => this.setState({open: false})}>
                    <Modal>
                        <Importer/>
                    </Modal>
                </Portal>
            </div>
        )
    }
}



