import { connect } from 'react-redux'
import { importerDirectoryChange } from '../actions'
import Mover from '../components/modals/MoverModal'

const mapStateToProps = state => {
    return {
        currentPath: state.importer.currentPath,
        documentsTree: state.importer.documentsTree,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onDirectoryChange: path => {
            dispatch(importerDirectoryChange(path))
        }
    }
}

const ConnectedMover = connect(
    mapStateToProps,
    mapDispatchToProps
)(Mover)

export default ConnectedMover


