import React, {Component} from 'react'
import classNames from 'classnames'

import button from '../../styles/buttons.scss'

import Loader from './Loader'
import Page from './Page'
import DataTable from './DataTable'

import { getInfosFromSlug, getFields } from '../../scripts/dataFields'

import { STATUS_LOAD } from '../../constants'

export default class Data extends Component {

    constructor(props, context) {
        super(props, context)

        this.state = {
            edition: false,
            current: {}
        }

        console.log('Data', props)
    }

    componentWillMount () {
        this.infos = getInfosFromSlug(this.props.match.params.slug)
        this.id = this.props.match.params.id

        this.props.onLoadData(this.infos.doctype, this.id)      
    }

    componentWillReceiveProps(newProps) {
        this.state.current = {...newProps.current}
    }

    save() {
        this.setState({
            edition: false
        })

        this.props.dataSave(this.infos.doctype, this.state.current)
    }

    edit() {
        this.setState({
            edition: true
        })
    }

    remove() {
        this.props.dataRemove(this.infos.doctype, [this.props.current])
        .then(e => this.props.history.goBack())
    }

    renderData(doctype) {
        const fields = getFields(this.infos.doctype)
        if(!fields) return

        const { current } = this.state

        return (
            <DataTable>
                {fields.map((group, keygroup) => (
                    <DataTable.Group title={group.name} key={keygroup}>
                        {group.group.map((item, key) => {
                            return (
                                <DataTable.Item
                                key={key}
                                title={item.display}
                                value={current[item.name]}
                                type={item.type}
                                updatable={this.state.edition}
                                values={item.values}
                                onChange={(e) => current[item.name] = e}
                                />
                            );
                        })}
                    </DataTable.Group>
                ))}
            </DataTable>
        )
    }

    render() {
        const loading = this.props.status === STATUS_LOAD

        const actions = []

        const remove = (<button onClick={() => this.remove()} className={classNames(button.button, button.assertive)} title="Supprimer"><i className="ion-ios-trash-outline"></i><span className={button.text}>SUPPRIMER</span></button>)
        actions.push(remove)

        if(this.state.edition) {
            const save = (<button onClick={() => this.save()} className={classNames(button.button, button.stable)} title="Sauvegarder"><i className="fa fa-floppy-o"></i><span className={button.text}>SAUVEGARDER</span></button>)
            actions.unshift(save)
        } else {
            const edit = (<button onClick={() => this.edit()} className={classNames(button.button, button.stable)} title="Modifier"><i className="fa fa-edit"></i><span className={button.text}>MODIFIER</span></button>)
            actions.unshift(edit)            	
        }

        let title = this.infos.name
        if(this.props.current && this.props.current.name) title += '/'+this.props.current.name

        return (
            <Page title={title} subtitle={''} 
                actions={actions}
                onBackPress={() => this.props.history.goBack()}>

                <Loader display={loading}/>

                {!loading && this.renderData.bind(this)()}
            </Page>
        )
    }
}


