import React from 'react'
import Page from './Page'
import { Link } from 'react-router-dom'

export const Assistant = (props) => {

    return (
        <Page title="Assistant de donne">
            <ul>
                <li>
                    <Link replace to={"/assistant/diagnosis"}>Crer une donne de diagnostic</Link>
                </li>
                <li>
                    <Link replace to={"/assistant/homedata"}>Crer une donne de maison</Link>
                </li>
                <li>
                    <Link replace to={"/assistant/equipments"}>Crer une donne d'quipement</Link>
                </li>
                <li>
                    <Link replace to={"/assistant/works"}>Crer une donne de travaux</Link>
                </li>
                <li>
                    <Link replace to={"/assistant/maintenances"}>Crer une donne d'entretien</Link>
                </li>
            </ul>
        </Page>
    )
}

export default Assistant


