import { autoImport } from './AutoImport'
import { InitKonnector } from './InitKonnector'

export default () => {

    autoImport()
    .then(() => console.log('Auto import OK'))
    .catch(err => console.error('Auto import fail', err))

    InitKonnector()
    .then(code => console.log('Initialize Konnector', code))
    .catch(err => console.error('Initialize Konnector fail', err))

}


