import React, {Component} from 'react'
import style from '../../styles/Navigation.sass'
import { withRouter, Link } from 'react-router-dom'
import classNames from 'classnames'

import Gauge from '../../containers/Gauge'

export const Navigation = (props) => {
    return (
        <nav className={style.main}>
           <div className={style.nav}>
                <ul className={style.list}>
                    <li className={style.button}>
                        <ActiveLink to="/Documents" onClick={() => console.log('Open documents')} activeClassName={style.active}>
                            <i className="ion-ios-home"></i>Mon logement
                        </ActiveLink>
                    </li>
                    <li className={style.button}>
                        <ActiveLink to="/contacts" onClick={() => console.log('Open contacts')} activeClassName={style.active}>
                            <i className="ion-ios-bookmarks-outline"></i>Contacts
                        </ActiveLink>
                    </li>
                    <li className={style.button}>
                        <ActiveLink to="/rappels" onClick={() => console.log('Open rappels')} activeClassName={style.active}>
                            <i className="ion-ios-checkmark-outline"></i>Rappels
                        </ActiveLink>
                    </li>
                    <li className={style.button}>
                        <ActiveLink to="/corbeille" onClick={() => console.log('Open corbeille')} activeClassName={style.active}>
                            <i className="ion-ios-trash-outline"></i>Corbeille
                        </ActiveLink>
                    </li>
                    {/* <li className={style.button}>
                        <ActiveLink to="/assistant" onClick={() => console.log('Open Assistant')} activeClassName={style.active}>
                            <i className="ion-ios-information-outline"></i>Assistant
                        </ActiveLink>
                    </li> */}
                </ul>
           </div>
           {/* <Gauge/> */}
           <div className={style.other}>
                <ul className={style.list}>
                    <li className={style.button}>
                        <ActiveLink to="/actualites" onClick={() => console.log('Open actualites')} activeClassName={style.active}>
                            Actualits
                        </ActiveLink>
                    </li>
                    <li className={style.button}>
                        <ActiveLink to="/preferences" onClick={() => console.log('Open preferences')} activeClassName={style.active}>
                            Prfrences
                        </ActiveLink>
                    </li>
                    <li className={style.button}>
                        <ActiveLink to="/aide" onClick={() => console.log('Open help')} activeClassName={style.active}>
                            Aide
                        </ActiveLink>
                    </li>
                    <li className={style.button}>
                        <ActiveLink to="/a-propos" onClick={() => console.log('Open about')} activeClassName={style.active}>
                             propos
                        </ActiveLink>
                    </li>
                </ul>
           </div>
        </nav>
    );
}

export const CustomLink = (props) => {
    const open = (e) => {
        e.preventDefault()
        props.onClick()
        props.history.push(props.to)
    }

    const { router, location, to, activeClassName, children } = props

    const active = location.pathname.indexOf(to) === 0

    return (
        <a className={active && activeClassName} onClick={e => open(e)}>
            {children}
        </a>
    )
}

const ActiveLink = withRouter(CustomLink)

export default Navigation


