

export const InitKonnector = async() => {

    //1. Detect if a worker Konnector is alwready running
    const triggers = await cozy.client.fetchJSON('GET', '/jobs/triggers')
    const konnectorTrigger = triggers.filter(e => e.attributes.worker === 'konnector')
    if(konnectorTrigger.length === 1) {
        return 'OK'

    } else if(konnectorTrigger.length > 1) {
        console.warn('More than 1 konnector Trigger set', konnectorTrigger)

        //Remove all konnector and create a new one
        for(const t of konnectorTrigger) {
            try {
                await cozy.client.fetchJSON('DELETE', '/jobs/triggers/'+t._id)
            } catch (error) {}
        }
    }

    //2. Create trigger arguments
    const data = {
        attributes : {
            type: '@cron',
            arguments : `0 */8 * * *`, //Every 8 hours
            worker: 'konnector',
            worker_arguments: {
                konnector: 'homebook-news'
            }
        }
    }

    //3. Create the trigger
    try {
        const trigger = await cozy.client.fetchJSON('POST', '/jobs/triggers', {data})
    } catch (error) {
        console.error('Error in trigger creation', error)
    }

    return 'NEW'
}

export default InitKonnector


