import React, {Component} from 'react'
import _ from 'lodash'
import style from '../../styles/ImporterModal.sass'
import button from '../../styles/buttons.scss'
import classNames from 'classnames'
import DirectoryPicker from '../elements/DirectoryPicker'
import Dropzone from 'react-dropzone'
import { getTypesByGroup } from '../../scripts/fileTypes'

import { VIEW_DIR, VIEW_FILE, ROOT_NAME, ROOT_PATH, STATUS_DONE, STATUS_ERROR, STATUS_LOAD } from '../../constants'

var ImportDocument = ({item, currentType, onDocChange}) => {

    const typesByGroup = getTypesByGroup()

    if(!item.typeFile) {
        if (currentType!='') {
            item.typeFile = currentType
        } else {
            item.typeFile = ''
        }
    }
    var preview = '/images/file.png'

    if(/^image\//.test(item.type)) {
        preview = item.preview
    }

    return (
        <div className={style.doc} onClick={e => e.stopPropagation()}>
            <div className={style.image}>
                <img src={preview} alt="document"/>
            </div>
            <span className={style.docName}>{item.name}</span>
            <select className={style.select} value={item.typeFile} onChange={(e) => {item.typeFile = e.target.value; onDocChange()}}>
                <option value="">Choisir un type</option>
                {typesByGroup.map((item, key1) => (
                    <optgroup label={item.name} key={key1}>
                        {item.types.map((type, key2) => (
                            <option key={key2} value={type.key}>{type.name}</option>
                        ))}
                    </optgroup>
                ))}
                <option value="">Autre</option>
            </select>
        </div>
    )
}

export default class ImporterModal extends Component {

    constructor(props, context) {
        super(props, context)
        this.state = {
            documents: [],
            dropzoneActive: false
        }
    }

    componentWillReceiveProps(props, context) {

        // Rinitialise la modal
        this.setState({
            dropzoneActive: false
        })
    }

    cancel(e) {
        e.stopPropagation()
        this.props.closePortal()
    }

    next() {
        this.props.onChangeView(VIEW_DIR)
    }

    prev() {
        this.props.onChangeView(VIEW_FILE)
    }

    import(e) {
        if(this.state.documents.length > 0) {
            cozy.client.files.statByPath(this.props.currentPath)
            .then(stat => {
                console.log('Parent', stat);
                this.props.onImport(this.state.documents, stat._id)
            })
        }
        this.cancel(e)
    }

    _dropFiles(files) {
        console.log('Files', files)

        var oldDocs = []
        for(let oldD of this.state.documents) {
            let ok = true
            for(let newD of files) {
                if(newD.name === oldD.name) {
                    ok = false
                    break
                }
            }
            if(ok) oldDocs.push(oldD)
        }

        if(this.props.currentType) {
            for(let d of files) {
                console.log("Default", this.props.currentType)
                d.typeFile = this.props.currentType
            }
        }

        this.setState({
            dropzoneActive: false,
            documents: [
                ...oldDocs,
                ...files
            ]
        })
    }

    _dragEnter(e) {
        if(e.dataTransfer.types[0] === "Files") {
            this.setState({
                dropzoneActive: true
            })
        }
    }

    _dragLeave(e) {
        this.setState({
            dropzoneActive: false
        })
    }

    render() {

        return (
            <div onClick={e => e.stopPropagation()} className={style.main}>
                {this.props.view === VIEW_FILE && (
                    <div className={style.file}>
                        <div className={style.header}>
                            <span>AJOUTER DES FICHIERS</span>
                            <button className={classNames(button.button, button.stable, button.clear)} onClick={(e) => this.cancel(e)}>
                                <i className="ion-ios-close-empty"></i>
                            </button>
                        </div>

                        <div className={style.content}>
                            <Dropzone
                            ref="dropzone"
                            disableClick={false}
                            disablePreview={false}
                            className={classNames(style.dropzone, {[style.active]: this.state.dropzoneActive})}
                            onDrop={(e) => this._dropFiles(e)}
                            onDragEnter={(e) => this._dragEnter(e)}
                            onDragLeave={(e) => this._dragLeave(e)}
                            inputProps={{capture: true}}
                            >
                                {this.state.documents.map((doc, key) => (
                                    <ImportDocument key={key} item={doc} onDocChange={() => this.setState({})} currentType={this.props.currentType}/>
                                ))}
                                {this.state.documents.length == 0 ? (<span className={style.message}>dposez vos fichiers ici</span>) : null}
                            </Dropzone>
                        </div>

                        <div className={style.footer}>
                            <button type="button" className={classNames(button.button, button.stable)} onClick={(e) => this.cancel(e)}>
                                <i className="ion-ios-close-empty"/>ANNULER
                            </button>
                            <button type="button" className={classNames(button.button, button.stable)} onClick={() => this.next()}>
                                <i className="ion-ios-folder-outline"/>CHANGER EMPLACEMENT
                            </button>
                            <button type="button" className={classNames(button.button, button.stable)} onClick={(e) => this.import(e)}>
                                <i className="ion-ios-cloud-upload-outline"/>OK
                            </button>
                        </div>
                    </div>
                )}

                {this.props.view === VIEW_DIR && (
                    <div className={style.dir}>
                        <div className={style.header}>
                            <span>CHOISIR UN EMPLACEMENT</span>
                            <button className={classNames(button.button, button.stable, button.clear)} onClick={(e) => this.cancel(e)}>
                                <i className="ion-ios-close-empty"></i>
                            </button>
                        </div>

                        <div className={style.content}>
                            <DirectoryPicker 
                                dirs={this.props.documentsTree} 
                                root={ROOT_PATH} 
                                default={this.props.currentPath} 
                                rootDirectoryName={ROOT_NAME} 
                                onPathChange={(path) => this.props.onDirectoryChange(path)}/>
                        </div>

                        <div className={style.footer}>
                            <button type="button" className={classNames(button.button, button.stable)} onClick={() => this.prev()}>
                                <i className="ion-ios-arrow-left"/>RETOUR
                            </button>
                            <button type="button" className={classNames(button.button, button.stable)} onClick={(e) => this.import(e)}>
                                <i className="ion-ios-cloud-upload-outline"/>IMPORTER
                            </button>
                        </div>
                    </div>
                )}
            </div>
        )
    }
}



