import { IMPORTER_FILE_UPLOAD_SUCESS, GAUGE_INITIALIZE_SUCCESS, GAUGE_INITIALIZE_FAILURE, GAUGE_INITIALIZE } from '../actions'

import { STATUS_DONE, STATUS_ERROR, STATUS_LOAD } from '../constants'

const defaultState = {
    usedTypes: [],
    status: STATUS_LOAD
}

export const gauge = (state = defaultState, action) => {
    switch (action.type) {
        case IMPORTER_FILE_UPLOAD_SUCESS:
            const types = []
            for(let file of action.files) {
                const type = getTypeOf(file)

                if(type === undefined) continue

                let find = false
                for(let t of state.usedTypes) {
                    if(t.type === type) {
                        t.count = t.count + 1 || 1
                        find = true
                        break
                    }
                }
                if(!find) {
                    console.log('Add type', type);
                    types.push({type, count:1})
                }
            }

            return {...state, usedTypes: [...state.usedTypes, ...types]}

        case GAUGE_INITIALIZE:
            return {...state, status: STATUS_LOAD}
        case GAUGE_INITIALIZE_SUCCESS:
            return {...state, usedTypes: action.types, status: STATUS_DONE}
        case GAUGE_INITIALIZE_FAILURE:
            return {...state, status: STATUS_ERROR}

        default:
            return state
    }
}

export default gauge

const getTypeOf = file => {
    return file.tags && file.tags[0];
}


