import React, {Component} from 'react'
import classNames from 'classnames'
import moment from 'moment'
import _ from 'lodash'

import Page from './Page'
import button from '../../styles/buttons.scss'

import CustomInput from '../utils/CustomInput'
import { ROOT_NAME } from '../../constants'
import { getInfosFromSlug, getFields } from '../../scripts/dataFields'

export default class DataForm extends Component {

    constructor(props, context) {
        super(props, context)

        this.state = {
            values: {},
            step: 0
        }
    }

    componentWillMount() {
        this.infos = getInfosFromSlug(this.props.match.params.slug)
        this.fields = getFields(this.infos.doctype)
    }

    nextStep(nb = 1) {
        const maxStep = this.fields[0].group.length - 1
        const questions = this.fields[0].group
        const q = questions[this.state.step]

        if(q.required && this.state.values[q.name] === undefined || this.state.values[q.name] === '') return

        let step = this.state.step + nb

        if(step <= maxStep) {
            const nq = questions[step]

            if(nq.depend) {
                if(_.isObject(nq.depend)) {
                    if(this.state.values[nq.depend.key] !== nq.depend.value) return this.nextStep(nb + 1)
                } else if(nq.depend instanceof String) {
                    if(!this.state.values[nq.depend]) return this.nextStep()
                }
            }
            let values = {...this.state.values, [nq.name]: undefined}

            switch (nq.type) {
                case 'list':
                    values[nq.name] = nq.values[0]
                    break
                case 'number':
                    values[nq.name] = 0
                    break
                case 'date':
                    values[nq.name] = moment().format('DD/MM/YYYY')
                    break
                case 'contract':
                case 'address':
                    values[nq.name] = {}
                    break
                case 'text':
                case '':
                case undefined:
                    values[nq.name] = ''
            }
            console.log('Step', step, values);

            this.setState({step, values})
        } else {
            //Save the new data
            const data = {...this.state.values}
            this.props.onNewData(this.infos.doctype, data)

            this.props.history.replace(ROOT_NAME)
        }
    }

    change(value, name) {
        const values = {...this.state.values, [name]: value}

        this.setState({values})
    }

    render() {
        const questions = this.fields[0].group
        const q = questions[this.state.step]

        const value = this.state.values[q.name]
        const title = "Questionnaire " + this.infos.name

        return (
             <Page title={title}>
                
                <div className="content">
                    <h3>{q.question || q.display}</h3>
                    <CustomInput style={{width: '100%', border: 'none', borderBottom: '1px solid #ddd', fontSize: '20px', outline: 'none', padding: '5px'}} autoFocus type={q.type} value={value} values={q.values} placeholder={q.display} onChange={e => this.change(e, q.name)}/>
                </div>
                <div className="footer" style={{margin: '20px 0'}}>
                    <button onClick={() => this.nextStep()} className={classNames(button.button, button.default)} title="Suivant" disabled={q.required && value === undefined || value === ''}>
                        <i className="ion-ios-arrow-thin-right"></i>Suivant
                    </button>
                </div>

                {/* jauge de progression */}

             </Page>
        )

    }

}


