import React, {Component} from 'react'
import classNames from 'classnames'

import {createOptionsFromArray} from '../../tools'
import button from '../../styles/buttons.scss'
import style from '../../styles/Modal.sass'

export default class InformationModal extends Component{

    constructor(props, context) {
        super(props, context)

        this.closing = setTimeout(() => {
            this.props.closePortal()
            this.props.onClose && this.props.onClose()
        }, this.props.timeout || 2000)
    }

    componentWillUnmount() {
        if (this.closing) {
            clearTimeout(this.closing)
        }
    }

    close(e) {
        e.stopPropagation()
        //close the portal
        this.props.closePortal()
        this.props.onClose && this.props.onClose()
    }

    stopClosing(e) {
        e.stopPropagation()

        if(this.closing){
            clearTimeout(this.closing)
        }
    }

    startClosing(e) {
        e.stopPropagation()

        this.closing = setTimeout(() => {
            this.props.closePortal()
            this.props.onClose && this.props.onClose()
        }, 1000)
    }

    render() {
        return (
            <div className={style.popup} onClick={e => e.stopPropagation()} onMouseEnter={e => this.stopClosing(e)} onMouseLeave={e => this.startClosing(e)}>
                <div className={style.header}>
                    <h1>{this.props.title || 'Informations'}</h1>

                    <button
                        onClick={e => this.close(e)}
                        className={classNames(button.button, button.stable, button.clear)}>
                        <i className="ion-ios-close-empty"></i>
                    </button>
                </div>
                <div className={style.content}>
                    <p>{this.props.infos}</p>
                </div>
            </div>
        )
    }
}


