import React, {Component} from 'react'
import style from "../../styles/DirectoryPicker.sass"
import classNames from 'classnames'
import _ from 'lodash'

export default class DirectoryPicker extends React.Component {
    constructor(props, context) {
        super(props, context)

        console.log('Tree', this.props.dirs)

        if(!this.props.root) return

        this.default = this.props.default
        this.rootDirectoryName = this.props.rootDirectoryName

        this.state = {
            current: this.props.default || this.props.root
        }
    }

    _onItemClick(path) {
        if(!path) return
        this.setState({
            current: path
        })

        if (_.isFunction(this.props.onPathChange))
            this.props.onPathChange(path)
    }

    _toggleDir(path) {

        var ok = this.toggleDirRec(path, this.props.dirs.childs)

        console.log("BD click ok ",ok)

        if(ok) {
            this.setState({})
        }

    }

    toggleDirRec(path, array) {
        for(let d of array) {
            if(d.path === path) {
                d.open = !d.open
                return true
            } else {
                if(d.childs && d.childs.length > 0) {
                    let ok = this.toggleDirRec(path, d.childs)
                    if(ok) return true
                }
            }
        }
        return false
    }

    _renderDirs() {
        if(this.props.dirs) {
            var list =  this.createDirList(this.props.dirs.childs)
            var displayName = this.rootDirectoryName
            return (
                <div className={style.container}>
                    <span className={classNames(style.name, {[style.selected]: this.props.dirs.path === this.state.current})} onClick={()=>this._onItemClick(this.props.dirs.path)}>
                        <i className={classNames(style.icon, "ion-ios-arrow-down")}></i><i className={classNames(style.icon, "ion-ios-folder-outline")}></i>{displayName}
                    </span>
                    {list}
                </div>
            )
        }
        return null
    }

    createDirList(list = []) {
        if(list.length === 0) return null
        var ret = []
        for(var l of list) {
            var displayName = l.name.substring(l.name.lastIndexOf('/')+1)
            ret.push(
                <li key={l.name}>
                    <span className={classNames(style.name, {[style.selected]: l.path === this.state.current})}
                        data-path={l.path}
                        onDoubleClick={(e) => this._toggleDir(e.target.dataset.path)}
                        onClick={(e) => this._onItemClick(e.target.dataset.path)}
                    >
                        <i onClick={(e) => this._toggleDir(e.target.dataset.path)} data-path={l.path} className={classNames(style.icon, {'ion-ios-arrow-right': l.childs.length > 0 && !l.open, 'ion-ios-arrow-down': l.childs.length > 0 && l.open})}></i>
                        <i className={classNames(style.icon, "ion-ios-folder-outline")}></i>{displayName}
                    </span>
                    {l.open && l.childs.length > 0 ? this.createDirList(l.childs) : null}
                </li>
            )
        }
        return (<ul className={style.content}>{ret}</ul>)
    }

    render() {
        return (
            <div className={style.main}>
                {this._renderDirs.bind(this)()}
            </div>
        )
    }
}



