import React, {Component} from 'react'
import classNames from 'classnames'
import Portal from 'react-portal'
import button from '../../styles/buttons.scss'
import style from '../../styles/Contacts.sass'
import ToolBoxOptions from './ToolBoxOptions'
import _ from 'lodash'

import Page from './Page'
import Loader from './Loader'
import ContactList from './ContactList'

import Modal from '../modals/Modal'
import ContactsModal from '../modals/ContactsModal'

import { VIEW_LIST, STATUS_LOAD, STATUS_DONE } from '../../constants'

export default class Contacts extends Component{

    constructor(props, context) {
        super(props, context)

        this.state = {
            current: null,
            modalOpen: false,
            edition: false
        }
    }

    componentWillMount () {
        this.props.onLoadContacts()
    }

    componentWillReceiveProps (newProps) {
        if(newProps.elements.length !== newProps.count) newProps.onLoadContacts()
    }

    add() {
        this.setState({
            current: null,
            modalOpen: true,
            edition: true
        })
    }

    open(item) {
        this.setState({
            current: item,
            modalOpen: true
        })
    }

    select(item) {
        this.props.onSelectContact(item)
    }

    remove() {
        if(!this.state.current) return
        this.props.onRemoveContacts([this.state.current])
        this.setState({
            modalOpen: false,
            current: null
        })
    }

    addOrSaveContact(contact) {
        if(this.state.current) {
            this.props.onSaveContact(this.state.current._id, contact)
        } else {
            this.props.onNewContact(contact.firstName, contact)
        }
        this.setState({
            modalOpen: false,
            current: null
        })
    }

    render() {
        const { elements, count, selectedCount, status } = this.props

        const loading = status === STATUS_LOAD

        const sort = elements.sort(function(a, b){
            if(a.lastName < b.lastName) return -1;
            if(a.lastName > b.lastName) return 1;
            return 0;
        })

        const sortedContacts = _.groupBy(sort, item => {
            let name = item.lastName || item.firstName || '_';
            name = name.trim().toUpperCase();

            const firstLetter = name[0];

            if ((/[0-9]/g).test(firstLetter)) {
                return '#';
            }
            return firstLetter
        })

        const add = (<button onClick={() => this.add()} className={classNames(button.button, button.default)}><i className="ion-ios-plus-empty"></i>AJOUTER</button>)
        const toolbox = (
            <ToolBoxOptions>
                <ul>
                    <li onClick={() => {
                        this.props.onSelectAll()
                    }}>
                        <i className="ion-ios-checkmark-outline">TOUT SELECTIONNER</i>
                    </li>

                </ul>
            </ToolBoxOptions>
        )
        const remove = (<button onClick={() => this.props.onRemoveContacts(elements.filter(f => f.selected))} className={classNames(button.button, button.assertive)}><i className="ion-ios-trash-outline"></i>SUPPRIMER</button>)

        const actions = [add, toolbox]
        if(selectedCount > 0)
                actions.unshift(remove)

        return (
            <Page title="Contacts" subtitle={count !== null && (count + (count > 1 ? ' contacts' : ' contact'))}
            actions={actions}>
                <Loader display={loading}/>

                {!loading && elements.length > 0 && _.map(sortedContacts, (peoples, letter) => (
                    <div key={letter}>
                        <h3 className={style.letter}>{letter}</h3>
                        <ContactList view={VIEW_LIST} onClickItem={item => this.open(item)} onSelectItem={item => this.select(item)}>
                            {peoples}
                        </ContactList>
                    </div>
                ))}

                {!loading && elements.length === 0 && "Aucun contacts"}

                <Portal isOpened={this.state.modalOpen} closeOnEsc onClose={() => this.setState({modalOpen: false, current: null, edition: false})}>
                    <Modal>
                        <ContactsModal
                        item={this.state.current}
                        title={this.state.current ? "Modifier contact" : "Nouveau contact"}
                        onSave={contact => this.addOrSaveContact(contact)}
                        onRemove={() => this.remove()}
                        edit={this.state.edition}/>
                    </Modal>
                </Portal>

            </Page>
        )
    }
}



