import { connect } from 'react-redux'
import { dataLoad, dataSave, dataRemove } from '../actions'
import Data from '../components/elements/Data'

const mapStateToProps = state => {
    return {
        current: state.data.current,
        status: state.data.fetchStatus
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onLoadData: (doctype, id) => {
            dispatch(dataLoad(doctype, id))
        },
        dataSave: (doctype, data) => {
            return dispatch(dataSave(doctype, data._id, data))
        },
        dataRemove: (doctype, data) => {
            return dispatch(dataRemove(doctype, data))
        }
    }
}

const ConnectedData = connect(
    mapStateToProps,
    mapDispatchToProps
)(Data)

export default ConnectedData


