import React, {Component} from 'react'
import classNames from 'classnames'
import PDF from 'react-pdf-js';
import style from '../../styles/pdf.sass'

import Loader from '../elements/Loader'

export default class PDFViewer extends Component {

    constructor(props, context) {
        super(props, context)

        this.state = {
            page: 0,
            pages: 0,
            zoom: 1
        }
    }

    onDocumentComplete = (pages) => {
        this.setState({page: 1, pages});
    }

    onPageComplete = (page) => {
        this.setState({page});
    }

    handlePrevious = e => {
        e.preventDefault()
        if(this.state.page === 1) return

        this.setState({ page: this.state.page - 1 });
    }

    handleNext = e => {
        e.preventDefault()
        if(this.state.page === this.state.pages) return

        this.setState({ page: this.state.page + 1 });
    }

    handleZoomPlus = e => {
        e.preventDefault()

        if(this.state.zoom <2) {
            this.setState({zoom: this.state.zoom + 0.5})        
        }
    }

    handleZoomMinus = e => {
        e.preventDefault()

        if(this.state.zoom > 0) {
            this.setState({zoom: this.state.zoom - 0.5})
        }              
    }

    renderPagination = (page, pages, zoom=false) => {
        const previousButton = <li className={classNames("previous", style.previous, {disabled: page === 1})} onClick={this.handlePrevious}>
            <a><i className="fa fa-arrow-left"></i></a>
        </li>

        const nextButton = <li className={classNames("next", style.next, {disabled: page === pages})} onClick={this.handleNext}>
            <a><i className="fa fa-arrow-right"></i></a>
        </li>

        let zoomButtons;
        if(zoom) {            
            zoomButtons = <li className={classNames("zoom", style.zoom)} >
                <a onClick={this.handleZoomMinus}>
                    <i className="fa fa-search-minus"></i>
                </a> <a onClick={this.handleZoomPlus}>
                    <i className="fa fa-search-plus"></i>
                </a>
            </li>
        }

        return (
            <nav>
                <ul className="pager">
                    {previousButton}
                    {zoom && (zoomButtons)}
                    {nextButton}
                </ul>
            </nav>
        );
    }

    render() {
        let pagination = null;
        if (this.state.pages) {
            pagination = this.renderPagination(this.state.page, this.state.pages, true);
        }
        return (
            <div>
                <div style={{textAlign: 'center', overflow: 'auto'}}>
                    <PDF
                    file={this.props.url}
                    onDocumentComplete={this.onDocumentComplete}
                    onPageComplete={this.onPageComplete}
                    page={this.state.page}
                    rotate={0}
                    scale={this.state.zoom}
                    loading={(<Loader/>)}/>
                </div>
                {pagination}
                <p style={{textAlign: 'center'}}>{this.state.page+' / '+this.state.pages}</p>
            </div>
        )
    }
}


