import React, {Component} from 'react'
import modal from '../../styles/Modal.sass'

export const Modal = ({ closePortal, children }) => {

    if (typeof children.type === 'function') {
        children = React.cloneElement(children, { closePortal });
    }

    return (
        <div className={modal.modal} onClick={closePortal}>
            {children}
        </div>
    )
}

export default Modal


