import React, {Component} from 'react'
import classNames from 'classnames'
import style from '../../styles/ToolBoxOptions.sass'
import button from '../../styles/buttons.scss'

export default class ToolBoxOptions extends Component {
    constructor(props, context) {
        super(props, context)
        this.state = {
            open: false
        }

        this.intent = null
    }

    componentWillUnmount() {
        clearTimeout(this.intent)        
    }

    open() {
        if(this.state.open)
            return

        this.setState({
            open: true
        })
        this.props.onOpen && this.props.onOpen()
    }

    close() {
        if(!this.state.open)
            return

        this.setState({
            open: false
        })
        this.props.onClose && this.props.onClose()
    }

    toggle() {
        if (this.state.open)
            this.close()
        else
            this.open()
    }

    closeIntent() {
        if(this.props.noAuto) return
        this.intent = setTimeout(() => this.close(), 100)
    }

    render() {
        return (
            <div className={style.toolBox}>

                <button type="button" onClick={() => this.toggle()} onBlur={(e) => this.closeIntent()} className={classNames(button.button, button.stable)}>
                    <i className="ion-more"></i>
                </button>

                {this.state.open && (<div className={style.content}>
                    <div className={style.triangle}></div>
                    {this.props.children}
                </div>)}
            </div>
        )
    }
}



